import { format } from 'date-fns';
import PropTypes from 'prop-types';

import Box from '@mui/material/Box';
import MenuItem from '@mui/material/MenuItem';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import IconButton from '@mui/material/IconButton';
import ListItemText from '@mui/material/ListItemText';

// import { useRouter } from 'src/routes/hooks';

import { Badge, Typography } from '@mui/material';

import { useTranslate } from 'src/locales';

import Label from 'src/components/label';
import Iconify from 'src/components/iconify';
import CustomPopover, { usePopover } from 'src/components/custom-popover';

// ----------------------------------------------------------------------

export default function AppointmentsTableRow({
  row,
  selected,
  onSelectRow,
  refetch,
  onViewRow,
  onBookAppoint,
  onDelayRow,
  onCancelRow,
  onUnCancelRow,
  onDeleteRow,
  unread
}) {
  const {
    code,
    subject,
    priority,
    category,
    assigned_to,
    status,
    // user,
    created_at,
    user_creation,
    ip_address_user_creation,
    updated_at,
    user_modification,
    ip_address_user_modification,
    modifications_nums,
  } = row;

  const { t } = useTranslate();

  // const router = useRouter();

  const popover = usePopover();
  const DDL = usePopover();
  return (
    <>
      <TableRow hover selected={selected}>
        {/* <TableCell align="center">
          <ListItemText
            primary={
              isValid(new Date(start_time)) &&
              new Date(start_time).toLocaleTimeString(t('en-US'), {
                timeZone: unit_service?.country?.time_zone,
                hour: '2-digit',
                minute: '2-digit',
              })
            }
            secondary={
              isValid(new Date(start_time)) &&
              new Date(start_time).toLocaleDateString(t('en-US'), {
                timeZone:
                  unit_service?.country?.time_zone ||
                  Intl.DateTimeFormat().resolvedOptions().timeZone,
              })
            }
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              mt: 0.5,
              component: 'span',
              typography: 'caption',
            }}
          />
        </TableCell> */}
        <TableCell
          sx={{
            cursor: 'pointer',
            color: '#3F54EB',
          }}
          onClick={onViewRow}
          align="center"
        >
          {code}
        </TableCell>
        <TableCell
          sx={{
            cursor: 'pointer',
            color: '#3F54EB',
          }}
          onClick={onViewRow}
          align="center"
        >
          <Badge badgeContent={unread} color="error">
            <Typography variant='body2' sx={{ p: unread ? 1 : 0 }}>{subject}</Typography>
          </Badge>
        </TableCell>
        <TableCell onClick={onViewRow} align="center">
          {priority}
        </TableCell>
        <TableCell onClick={onViewRow} align="center">
          {category?.name_english}
        </TableCell>
        <TableCell onClick={onViewRow} align="center">
          {assigned_to?.email}
        </TableCell>
        <TableCell align="center">
          <Label
            variant="soft"
            color={
              (status === 'pending' && 'warning') ||
              (status === 'waiting' && 'secondary') ||
              (status === 'processing' && 'info') ||
              (status === 'completed' && 'success') ||
              (status === 'closed' && 'error') ||
              'default'
            }
          >
            {t(status)}
          </Label>
        </TableCell>
        <TableCell align="center">{user_creation?.email}</TableCell>
        <TableCell align="center">
          <ListItemText
            primary={format(new Date(updated_at), 'dd MMM yyyy')}
            secondary={format(new Date(updated_at), 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </TableCell>
        <TableCell align="center">
          <ListItemText
            primary={format(new Date(created_at), 'dd MMM yyyy')}
            secondary={format(new Date(created_at), 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </TableCell>

        <TableCell align="right" sx={{ px: 1 }}>
          <IconButton color={popover.open ? 'inherit' : 'default'} onClick={popover.onOpen}>
            <Iconify icon="eva:more-vertical-fill" />
          </IconButton>
        </TableCell>
      </TableRow>

      {/* <BookManually refetch={refetch} appointment={row} open={Book.value} onClose={Book.onFalse} /> */}

      <CustomPopover
        open={popover.open}
        onClose={popover.onClose}
        arrow="right-top"
        sx={{ width: 155 }}
      >
        <MenuItem lang="ar" onClick={DDL.onOpen}>
          <Iconify icon="carbon:data-quality-definition" />
          {t('DDL')}
        </MenuItem>
      </CustomPopover>

      <CustomPopover
        open={DDL.open}
        onClose={DDL.onClose}
        arrow="right-top"
        sx={{
          padding: 2,
          fontSize: '14px',
        }}
      >
        <Box sx={{ fontWeight: 600 }}>{t('creation time')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>
          <ListItemText
            primary={format(new Date(created_at), 'dd MMM yyyy')}
            secondary={format(new Date(created_at), 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('created by')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{user_creation?.email}</Box>

        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('created by IP')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{ip_address_user_creation}</Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('editing time')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>
          <ListItemText
            primary={format(new Date(updated_at), 'dd MMM yyyy')}
            secondary={format(new Date(updated_at), 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('editor')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{user_modification?.email}</Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('editor IP')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray', fontWeight: '400' }}>
          {ip_address_user_modification}
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>
          {t('modifications no')}: {modifications_nums}
        </Box>
      </CustomPopover>
    </>
  );
}

AppointmentsTableRow.propTypes = {
  onDeleteRow: PropTypes.func,
  onCancelRow: PropTypes.func,
  onUnCancelRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onBookAppoint: PropTypes.func,
  onViewRow: PropTypes.func,
  onDelayRow: PropTypes.func,
  refetch: PropTypes.func,
  row: PropTypes.object,
  selected: PropTypes.bool,
  unread: PropTypes.number,
};
